<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="records"
      :search="search"
      class="elevation-2"
      :loading="loadingRecords"
      loading-text="Loading... Please wait"
    >
      <template v-slot:top>
        <div class="d-flex mx-4 pt-4 mb-4">
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            outlined
            dense
            hide-details
            :disabled="loadingRecords"
          ></v-text-field>
        </div>
        <v-divider></v-divider>
      </template>

      <template v-slot:[`item.subject`]="{ item }">
        {{ item.subject }}
        <span class="grey--text">#{{ item.id }}</span>
      </template>

      <template v-slot:[`item.priority_status`]="{ item }">
          <v-chip :color="getPriorityColor(item.priority_status_id)" small dark>
            {{ item.priority_status }}
          </v-chip>
        </template>

      <template v-slot:[`item.action`]="{ item }">
        <div v-show="$vuetify.breakpoint.mdAndUp ? true : false">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }" v-if="!item.assigned_to">
              <v-btn
                icon
                small
                color="primary"
                :to="{ name: 'MyTeamEdit', params: { id: item.id } }"
              >
                <v-icon small color="primary" v-bind="attrs" v-on="on">
                  mdi-pencil
                </v-icon>
              </v-btn>
            </template>
            <span>Edit</span>
          </v-tooltip>
        </div>

        <div v-show="$vuetify.breakpoint.mdAndUp ? false : true">
          <v-btn
            color="primary"
            x-small
            :to="{ name: 'MyTeamEdit', params: { id: item.id } }"
            v-if="!item.assigned_to"
          >
            Edit
          </v-btn>
        </div>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      search: null,
      headers: [
        { text: "Client", value: "client", class: "text-uppercase" },
        { text: "Subject", value: "subject", class: "text-uppercase" },
        {
          text: "Status",
          value: "ticket_status",
          class: "text-uppercase",
        },
        {
          text: "Priority",
          value: "priority_status",
          class: "text-uppercase",
        },
        {
          text: "Assigned To",
          value: "assigned_to",
          class: "text-uppercase",
        },
        {
          text: "Action",
          value: "action",
          class: "text-uppercase",
          sortable: false,
        },
      ],
    };
  },

  created() {
    this.$store.commit("myTeam/setRecords", []);
    this.$store.dispatch("myTeam/getRecords");
  },

  computed: {
    ...mapState("myTeam", ["loadingRecords", "records"]),
  },

  methods: {
    getPriorityColor(data) {
      switch (data) {
        case 1:
          return "green";
        case 2:
          return "blue";
        case 3:
          return "orange";
        case 4:
          return "red";
        default:
          return "grey";
      }
    },
  },
};
</script>